.membership-card {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 16px;
  padding: 20px;
  margin-bottom: 30px;
  color: white;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
}

.membership-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.status-badge {
  display: flex;
  align-items: center;
  gap: 8px;
  background: rgba(255, 255, 255, 0.2);
  padding: 8px 16px;
  border-radius: 20px;
  font-weight: 600;

  &.premium {
    background: rgba(255, 215, 0, 0.3);
  }

  mat-icon {
    font-size: 20px;
    width: 20px;
    height: 20px;
  }
}

.stats {
  display: flex;
  gap: 30px;

  .stat-item {
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    .label {
      font-size: 0.85rem;
      opacity: 0.9;
      margin-bottom: 4px;
    }

    .value {
      font-size: 1.5rem;
      font-weight: 700;

      &.warning {
        color: #ffd700;
      }

      &.unlimited {
        font-size: 2rem;
      }
    }
  }
}

.upgrade-button {
  width: 100%;
  height: 48px;
  font-weight: 600;

  mat-icon {
    margin-right: 8px;
  }
}

@media (max-width: 768px) {
  .membership-info {
    flex-direction: column;
    gap: 15px;
    align-items: flex-start;
  }

  .stats {
    width: 100%;
    justify-content: space-around;
  }
}
